/* header & navigation bar formatting */

header {
    height: 65px;
    text-align: center;
}
  
nav {
    width: 95%;
    height: 55px;
    margin: 0 25px;
    position: relative;
}
  
#nav_bar {
    background-color: #5d5c61;
    color: #c5d0db;
    height: 52px;
}
  
#nav_name_init {
    position: absolute;
    left: 20px;
    font-family: Ruthie, cursive;
    font-size: 50px;
    letter-spacing: 1px;
    background: none;
    color: #c5d0db;
    line-height: 1;
    padding-top: 5px;
}

#nav_name_init a {
    color: #c5d0db;
    text-decoration: none;
}
  
#nav_name_init a:hover {
    color: #aabbcc;
}

.nav_menu_item {
    font-size: 20px;
    padding: 0px 15px;
}
  
ul {
    list-style-type: none;
    position: absolute;
    right: 0;
}
  
li {
    float: left;
}
  
li a {
    display: block;
    color: #c5d0db;
    height: 55px;
    padding: 10px 10px;
}
  
li a:hover {
    color: #aabbcc;
}

