.container {
    color: #5d5c61;
    margin: 0 auto;
}

/* specific styles to support smaller screens */

@media only screen and (max-width: 640px) {
    .container {
        /* padding: 50px 0px; */
    }
}
.aboutMe {
    margin-top: 15px;
    margin-left: 20px;
    text-align: center;
}

#about_greeting {
    font-family: Ruthie, cursive;
    font-size: 75px;
    padding-top: 100px;
}

#about_whoIam {
    font-family: Ruthie, cursive;
    font-size: 35px;
    font-weight: normal;
    padding-top: 0px;
    padding-bottom: 100px;
}
.card {
    width: 18rem;
    margin: 20px;
}
  
.card-img-top {
    max-height: 175px;
    min-height: 175px;
}
  
.card-body {
    padding: 3px;
    background-color: #c5d0db;
    text-align: center;
}
  
.card-title,
.card-text {
    margin-bottom: 0;
    background-color: #c5d0db;
    color: #5d5c61;
}

.card-title {
    font-weight: bolder;
}

.card-text {
    text-align: left;
    margin-top: 5px;
    margin-left: 5px;
}

.card-footer {
    padding-top: 10px;
    padding-left: 3px;
    padding-bottom: 20px;
    background-color: #c5d0db;
    text-align: center;
}
  
.tooltip-inner {
    background-color: #151411;
    color: #bbc8d7;
    text-align: left;
    font-size: 16px;
    border: 1px solid black;
    border-radius: 10px;
  }

.fa-github {
    float: right;
    vertical-align: middle;
    margin-right: 3px;
}
  
.skillicon,
.skilliconS,
.skilliconL {
    height: 30px;
    float: left;
    margin-left: 5px;
    margin-top: 5px;
}

.skilliconS {
    height: 20px;
    margin-top: 10px;
}

.skilliconL {
    height: 40px;
    margin-top: 0px;
}

@media only screen and (max-width: 768px) {

  .tooltip-inner {
    font-size: 14px;
  }

}

