}
footer {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 35px;
    line-height: 35px;
    font-family: Ruthie, cursive;
    font-size: 30px;
    text-align: center;
    background-color: #5d5c61;
    color: #c5d0db;
    vertical-align: 25%;
}

.footer_icon {
    float: right;
    font-size: 25px;
    padding-top: 5px;
    padding-right: 10px;
}
  
.footer_icon_link {
    color: #c5d0db;
}

.footer_icon_link:hover {
    color: #aabbcc;
}
html {
  position: relative;
  min-height: 100%;
}

.clearfix::after {
  content: "";
  clear: both;
  display: table;
}

body {
  font-family:'Philosopher', sans-serif;
  background: #c5d0db;
  color: #5d5c61;
  height: 100%;
  margin: auto;
  margin-bottom: 40px;
}

h2 {
  color: #5d5c61;
  font-size: 25px;
  font-weight: bold;
  border-bottom: 2px solid #5d5c61;
  padding: 20px 0px;
  margin-bottom: 0;
}

h3 {
  background-color: #c5d0db;
  color: #5d5c61;
  font-family:'Shadows Into Light Two', cursive;
  font-size: 20px;
  margin-bottom: 0;
}

h5 {
  margin-top: 10px;
}

h6 {
  font-size: 18px;
  text-align: left;
  vertical-align: middle;
  clear: both;
}

a {
  text-decoration: none;
}

a:hover {
  text-decoration: none;
}
